.App{
  background-color:mediumslateblue; /* Default background color for container */
  width: 1600px;
  height: 1200px;
  position: fixed;
}
.navbar {
  width: 1490px;
  display: flex;
  justify-content: space-between; 
  align-items: center; 
  padding: 10px 20px; 
  background-color: #f0f0f0;
  text-decoration: none;
}

ul {
  display: flex; 
  margin: 0;
  padding: 0; 
}

li {
  list-style-type: none;
  text-decoration: none;
  margin-left: 40px;
  
}

img {
  height: 30px; 
  text-decoration: none;
}

.navbar a{
  text-decoration: none;
  
}
.Link{
  color: black;
}
.navbar a {
  text-decoration: none;
}

.navbar a:visited {
  text-decoration: underline; /* Underline visited links */
}

.navbar a:focus, .navbar a.active {
  text-decoration: underline; /* Underline focused or active links */
}
.form-inline {
  display: flex;
  align-items: center;
}

.form-control {
  width: 300px; /* Adjust width as needed */
  margin-right: 10px; /* Adjust margin as needed */
  
}

.btn {
  padding: 8px 16px; /* Adjust padding as needed */
  font-size: 16px; /* Adjust font size as needed */
}


/* Container */